import * as ArrayExtensions from '../arrayExtensions';

describe('ArrayExtensions.unique test', () => {
    it('should return [] for []', () => {
        expect(ArrayExtensions.unique([])).toEqual([]);
    });

    it('should return same array if there is no duplicate element', () => {
        const test = [1, 2, 3];
        expect(ArrayExtensions.unique(test)).toEqual(test);
    });

    it('should return an array with unique element', () => {
        const test = [1, 1, 2, 3];
        expect(ArrayExtensions.unique(test)).toEqual([1, 2, 3]);
    });
});